import React from "react";
import "./product-box.css";
import Image from "./ImageD/Image";
import Info from "./InfoD/Info";
import CardButton from "./CardBtnD/CardButton";

const ProductBox = ({imgUrl, title, data, card, setCard, count,setCount}) => {
    // Add Card
    const addCard = () => {
        let tempCard = [];
        tempCard = card;
        tempCard.push(data);
        setCard(tempCard);
        setCount(()=> count++);
        console.log(card);
    };
    return(
        <div className="product-box">
            <Image imgUrl={imgUrl} />
            <Info title={title}/>
            <CardButton addCard={addCard}/>
        </div>
    );
};

export default ProductBox;