import React, {useEffect, useState} from "react";
import './App.css';
import './components/SlickListD/slick-list.css';
import SlickList from "./components/SlickListD/SlickList";
import PrevButton from "./components/PrevButton";
import NextButton from "./components/NextButton";


function App() {
    // Basic
    const [count, setCount] = useState(0);
    const [data, setData] = useState([]);
    const [card, setCard] = useState([]);

    // Hooks
    useEffect(() => {
        async function getData(){
            const res = await fetch("https://jsonplaceholder.typicode.com/photos");
            const data = await res.json();
            setData(data.filter(data => (data.id <= 50)));
        }
        getData();
    }, []);

    // Slide
    const productSlide = document.querySelector(".slick-list");
    const slideTrack = document.querySelector(".slider-tracker");
    console.log(slideTrack);
    let counter = 0;
    function forward(){
        if (counter !== (data.length/5 - 1)){
            productSlide.style.transition = "transform .6s ease-in-out";
            counter++;
            productSlide.style.transform = "translateX(" + (-75 * counter) + "em)";
            hidePrevBtn(counter);
            hideNextBtn(counter);
        }
    }
    function backward(){
        if (counter !== 0){
            productSlide.style.transition = "transform .6s ease-in-out";
            counter--;
            productSlide.style.transform = "translateX(" + (-75 * counter) + "em)";
            hideNextBtn(counter);
            hidePrevBtn(counter);
        }
    }
    // Hiding Buttons
    const prevBtn = document.getElementById("prev");
    const nextBtn = document.getElementById("next");
    const hidePrevBtn = (e) => {
        return (e === 0 ? prevBtn.classList.add('hidden') : prevBtn.classList.remove("hidden"));
    };
    const hideNextBtn = (e) => {
      return (e === (data.length / 5 - 1) ? nextBtn.classList.add('hidden') : nextBtn.classList.remove("hidden"));
    };
    return (
        <div className="App">
            <div className="slider-track">
                <PrevButton backward={backward}/>
                <SlickList card={card} setCard={setCard} data={data} count={count} setCount={setCount} />
                <NextButton forward={forward} />
            </div>
            <div className="card-show-container">
                <div className="card-show-wrapper">
                    <i className="fa fa-shopping-cart" >

                    </i>
                    <h2>{count}</h2>
                    <h4>{`Last Added Product ID : ${(card.length ? (card[card.length - 1].id) : "" )}`}</h4>
                </div>
            </div>
        </div>
    );
}

export default App;
